
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  
}

.products {
  display: flex;
  height: 90vh;
  align-items:stretch;
  justify-content:center;
  font-size: 3rem;
  background:#ffffff ;
}

.home{
  max-height: fit-content;
  min-width: fit-content;
  background-color:	#ffffff
    }
.home .content h1{
  display:flex;
  
  align-content:center;
  justify-content:center;
  font-size: 5rem;
  
  background: -webkit-linear-gradient(#01011f, #000000);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.home .content p{

    display:flex;
  height: 90vh;
  align-content:center;
  justify-content:left;
  font-size: 1rem;
  margin-left: 5%;
  
}

.home .content .btn{
  width: 80%;
  height: 50px;
  margin-right: 20%;
  margin-top:35%;
  border-radius: 2px;
  background: linear-gradient(to bottom, #0033cc 0%, #33ccff 100%);
  outline: none;
  border: none;
  border-radius: 20px;
  color: rgb(255, 255, 255);
  font-size: 2rem;
}
.home .content .btn2{
  
  width: 80%;
  height: 50px;
  margin-right: 20%;
  margin-top:35%;
  border-radius: 2px;
  background: linear-gradient(to bottom, #0033cc 0%, #33ccff 100%);
  outline: none;
  border: none;
  border-radius: 20px;
  color: rgb(255, 255, 255);
  font-size: 2rem;
}

/* img 2 (according to height display in web)*/
